const app = getApp();
// 获取公共域名地址
import serverUrl from '@/common/serveUrl.js';

export default {
	data () {
		return {
			pic:'',
			globalData: {},
			usid: '',
			id: '',
			shopInfo:'',
		
			specClass: '',//规格弹窗css类，控制开关动画
			selectSpec:null,//选中规格
			sumPrice: 0,
			// 用户选择
			activeClass: null, 
			datailInfo: '',
			goodsList:[],
			imgUrl:this.$imgUrl
			// 商品信息
		}
	},
	
	onLoad(option) {
		this.globalData = app.globalData
		var id = option.id;
		this.usid = option.id
		console.log(id)
		var that = this;
		const url = serverUrl.serverUrl;
		// if (!app.globalData.userInfo) {
		// 	uni.showModal({
		// 		title: '提示',
		// 		content: '您尚未注册，是否马上去注册页面？',
		// 		success: function(sm) {
		// 			if (sm.confirm) {
		// 				uni.redirectTo({
		// 					url: '/pages/application/member',
		// 				})
		// 			} else {
		// 				uni.switchTab({
		// 					url: '/pages/index/index'
		// 				})
		// 			}
		// 		}
		// 	})
		// }
		uni.request({
			// url: url + '/app/Goods/findDetail',
			url: "https://wx.zzsnqyjxh.org/test/app/Goods/findDetail?id="+id,
			method: 'POST',
			success: res => {
				if (res.data.code == 200) {
					console.log(res)
					var datailInfo =res.data.data;
					that.datailInfo = datailInfo
					// that.pic = res.data.data;
						// console.log(that.datailInfo[1].detail)
						that.pic=that.datailInfo[1].detail
						console.log(that.pic)
				}
			}
		});
	},
	methods: {
		// 点击大家还喜欢商品
		toDatil(e){
			console.log(e)
			var id = e.currentTarget.dataset.index;
			uni.redirectTo({
				url: './shop-recommed?id='+id
			});
		},
		//查询商品详情
		shopDetail() {
			
		},
		//立即购买
		toggleSpec() {
			// if(this.activeClass==null){
			// 	return this.showSpec(()=>{
			// 		this.toConfirmation();
			// 	});
			// }
			this.toConfirmation();
		},
		//立即购买
		buy(){
			if(this.selectSpec==null){
				return this.showSpec(()=>{
					this.toConfirmation();
				});
			}
			this.toConfirmation();
		},
		//选择规格
		getItem(index){
			this.selectSpec = index;
		},
		//减少数量
		sub(){
			if(this.shopInfo.number<=1){
				return;
			}
			this.shopInfo.number--;
			var num = this.shopInfo.number
		},
		//增加数量
		add(){
			this.shopInfo.number++;
			var num = this.shopInfo.number
		},
		//规格弹窗
		showSpec(fun) {
			console.log('show');
			this.specClass = 'show';
			this.specCallback = fun;
		},
		specCallback(){
			return;
		},
		//关闭规格弹窗
		hideSpec() {
			this.specClass = 'hide';
			//回调
	
			this.selectSpec&&this.specCallback&&this.specCallback();
			this.specCallback = false;
			setTimeout(() => {
				this.specClass = 'none';
			}, 200);
		},
		//跳转确认订单页面
		toConfirmation(){
			let tmpList=[];
			let goods = {id:this.usid,img: this.datailInfo[1].goodsImg,name:this.shopInfo.goodsName,price:this.datailInfo[1].goodsBuyingPrice,number:this.shopInfo.number,details:this.shopInfo.goodsDetail };
			tmpList.push(goods);
			uni.setStorage({
				key:'buylist',
				data:tmpList,
				success: () => {
					uni.navigateTo({
						// url:'/pages/shop/order?id=id&num=num',
						url:'/pages/shop/order',
					})
				}
			})
		},
	}
}

